import { CITY, COUNTRY, FIRST_NAME, LAST_NAME, MAX_CITY_LENGTH, MAX_COUNTRY_LENGTH, MAX_FIRST_NAME_LENGTH, MAX_LAST_NAME_LENGTH, MAX_PASSWORD_LENGTH, MIN_CITY_LENGTH, MIN_COUNTRY_LENGTH, MIN_FIRST_NAME_LENGTH, MIN_LAST_NAME_LENGTH, MIN_PASSWORD_LENGTH, MIN_PHONE_LENGTH, PASSWORD, PHONE } from "../data/data"

export const  getRange = (type) => {
    switch (type) {
        case FIRST_NAME: return [MIN_FIRST_NAME_LENGTH, MAX_FIRST_NAME_LENGTH]
        case LAST_NAME: return [MIN_LAST_NAME_LENGTH, MAX_LAST_NAME_LENGTH]
        case PHONE: return [MIN_PHONE_LENGTH, MIN_PHONE_LENGTH]
        case PASSWORD: return [MIN_PASSWORD_LENGTH, MAX_PASSWORD_LENGTH]
        case COUNTRY: return [MIN_COUNTRY_LENGTH, MAX_COUNTRY_LENGTH]
        case CITY: return [MIN_CITY_LENGTH, MAX_CITY_LENGTH]
    }
}